@import url(https://fonts.googleapis.com/css2?family=Bangers&family=Big+Shoulders+Display:wght@300;500&family=Comfortaa:wght@400;700&display=swap);
.vreme {
    grid-row: 2/5;
    grid-column: 4/5;
    margin: 0;
    width: 90%;
    max-width: 450px;
    background-color: rgba(256, 256, 256, 0.5);
    text-align: center;
    padding-bottom: 3em;
    border-radius: 5em;
    border: 2px solid #999;
}

.cas {
    font-size: 5em;
    margin: 0.5em;
    font-family: Arial, Helvetica, sans-serif;
}

.vreme-ikona {
    max-width: 20em;
}

.temperatura {
    font-size: 6em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    margin-top: 0.05em;
    margin-bottom: 0.15em;
}

.veter {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 3em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}

.veter img {
    width: 25%;
}
.veter-text {
    line-height: 0.3em;
}
.vreme span {
    font-size: 30px;
    color: #444;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: lighter;
    line-height: 0;
}
.ostalo {
    grid-row: 4/5;
    grid-column: 2/4;
    justify-self: stretch;
    align-self: stretch;
}
.cenik {
    background-color: rgba(1, 1, 5, 0.5);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2em;
    margin: 1em;
    padding: 0.5em;
    border: 2px solid #8CBE1C;
    border-radius: 50px;
    text-align: center;
    overflow: hidden;
}
.cenik h3, .cenik p {
    margin: 0 0 10px 0;
}
.cenik h3 {
    font-family: 'Bangers', cursive;
    letter-spacing: 2px;
    color: #c0da89;
    border-bottom: 1px solid #8CBE1C;
}

.cenik p {
    font-family: 'Big Shoulders Display', cursive;
    color: #8CBE1C;
    font-size: 1.1em;
    font-weight: 500;
}
.cenik table{  
    display: -webkit-flex;  
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content:  center;
            justify-content:  center;
    margin: auto;
}
.cenik table tr {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
}
.cenik table td, .cenik table th {
    border: 1px solid #8CBE1C;
    font-family: 'Comfortaa', cursive;
    padding: 0.3em 0.7em;
}
.cenik table td {
    border-bottom: none;
    color: #c0da89;
    font-weight: 700;
}
.cenik table th {
    color: #8CBE1C;
    border-top: none;
    font-weight: 400;
}
.cenik table tr th:first-child {
    border-left: none;
}
.cenik table tr:last-child th{
    border-right: none;
}
.cenik table tr:last-child td{
    border-right: none;
}
.cenik table tr:first-child  td{
    border-left: none;
}

